function FormAction({caption, onClick}){
    return(
        <button onClick={onClick} className="bg-pink-500 border-none px-2 ml-16 mt-1 py-1 rounded-xl cursor-pointer text-white shadow-xl hover:shadow-inner transition duration-500 ease-in-out  transform hover:-translate-x hover:scale-105">
        {caption}
      </button>
    );
}
export default FormAction;