import React, {useState} from 'react';
import Form from '../Forms/Form';
import FormField from '../Forms/FormField';
import calathea from './calathea.jpg';
import FormAction from '../Forms/FormAction';
function Login(){
    const [ formData,setFormData]=useState({
        username:"",
        password:"",
        usernameError:"",
        passwordError:""
    });

    const _self = this;
    const myOnChange =function(e){
        const {name,value}=e.currentTarget;
        let error =null;
        if(name==="password"){
            error =(/^(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[^\w\d\s:])([^\s]){8,32}$/).test(value) ? null : "La Contraseña es inválida."
        }
        setFormData({...formData,[name]:value,[name + "Error"]:error});
    }

    return(
       
        <section className="flex  border rounded-lg bg-white w-2/4 md:h-96 md:w-1/3 sm:w-full shadow-lg" >
            
             <section className="flex flex-col justify-center sm:m-11 md:m-16 items-center ">
                <div className=" mb-10 flex justify-center intems-center justify-items-center">
                <h2 className=" text-center text-lg">Inicio de Sesión</h2>
                </div>
                <Form >
                
                
                    <FormField  type="text" id="username" fname="username" onChange={myOnChange} caption="Correo Electrónico" value={formData.username} error={formData.usernameError} placeholder="Usuario"></FormField>
                    <FormField type="password" id="password" fname="password" onChange={myOnChange} caption="Contraseña" value={formData.password} error={formData.passwordError} placeholder="Octopus123"></FormField>
                    <FormAction caption="Enviar" onClick={(e)=>{
                        e.preventDefault();
                        e.stopPropagation();
                        const { username, password } = formData;
                        alert(`Usuario:${username} Psswd:${password}`);}}></FormAction>
                </Form>
                <section className="flex mt-2 items-center text-center">
                <hr class="border-gray-300 border-1 w-full rounded-md"/>
                    <label class="block font-medium text-sm text-gray-700 w-full">
                                        Accede con
                    </label>
                <hr class="border-gray-300 border-1 w-full rounded-md"/> 

                </section>

                <section class="flex mt-1 justify-center w-full">
                    <button class="sm:mr-0 mr-5 bg-blue-500 border-none px-4 py-2 rounded-xl cursor-pointer text-white shadow-xl hover:shadow-inner transition duration-500 ease-in-out  transform hover:-translate-x hover:scale-105">Facebook</button>
                    <button class="bg-red-500 border-none px-4 py-2 rounded-xl cursor-pointer text-white shadow-xl hover:shadow-inner transition duration-500 ease-in-out  transform hover:-translate-x hover:scale-105">Google</button>
                </section>
                                
            </section>

            
           <section  className=" md:flex sm:hidden">
               <img src={calathea} className="h-96 rounded-lg"/>
           </section>
        
        </section>
    );
}
export default Login;